<template>
	<div  class="my-switch">
		 <span  :class="{ active: isActive }" @click="changeUp">上班</span><span :class="{ active: !isActive }" @click="changeDown">下班</span>
	</div>
</template>

<script>
export default {
	data () {
		return {
			isActive:true,
			// selfTest:this.test
		}
	},
	props:{
		test:{
			type:Object,
		}
	},
	computed: {
	
	},
	created () {
		console.log(this.$attrs.name)
	},
	mounted () {

	},
	methods: {
		changeUp(){
			this.isActive=true;
		
		},
		changeDown(){
			this.isActive=false;
		}
	}
}
</script>
<style>
   .my-switch{
	   position: relative;
	   width: 164px;
	   height: 44px;
	   border: 1px solid #0C1523;
		 background-color: #0C1523;
		 color: #fff;
	   top: 13px;
	   padding: 4px 6px;
		 border-radius: 22px;
		 cursor: pointer;
   }
    .my-switch>span{
		display: inline-block;
		width: 75px;
		height: 34px;
		text-align: center;
		line-height: 34px;
		border-radius: 17px;
		color: #fff;
		cursor: pointer;
	
	}
	.active{
	  background-color: #0f74e6
	}

</style>



