<template>
	<div>
		<el-container>
			<el-header id="inner-head">
				<div class="doorway-set">
					<span class="doorway-set-icon"></span>
					<span>设置显示门道</span>
				</div>
				<div class="mode-set">
					<span class="mode-set-icon"></span>
					<span>设置显示模式</span>
				</div>
			
				<div class="btn-logout">
					<span class="logout-icon"></span>
					<span>退出</span>
				</div>

				<div class="header-user-info">
					<span class="user-icon"></span>
					<span>admin1</span>
				</div>
				<div class="work-state-toggle">
					 <mySwitch :name="name"></mySwitch>
				</div>
			</el-header>
			<el-main>
		
			

			</el-main>
		</el-container>
	</div>
</template>

<script>
// import '@/styles/innercommon.css'
import {Ajax} from '@/js/ajax'
import mySwitch from '@/components/inner/my-switch'
export default {
	components:{
		mySwitch
	},
	data () {
		return {
		value4:false,
		name:"gugugu",
		}
	},
	created(){
	
	},
	mounted () {

	},
	methods: {
	
		test () {
			Ajax({
				method: 'post',
				url: '/init',
				data: {},
				done: function (err, data, res) {
					if (err) {
						console.log(err);
					} else {
						console.log(data);
					}
				}
			})
		}
	}
}

</script>
<style lang="scss" src="@/styles/innercommon.scss" scoped>
</style>
















